import React from "react";
import { Capacity } from "../../../../../../core/interfaces/Solution";
import { LogoCapabilities } from "../../../../../../core/constants/logo-capabilities";

export const CardArtifactDesktop: React.FC<{
  capabilities: Capacity[],
  dark?: boolean,
}> = (props) => {
  return (
    <React.Fragment>
      {props.capabilities.map((capacity, index) => {
        return (
          <div
            key={`${capacity.name}-desktop-${index}`}
            className="col-sm-4 d-flex align-items-stretch"
          >
            <div className={props.dark? "card-dark" : "card"}>
              <img
                className="card-header text-center"
                src={LogoCapabilities[capacity.logo]}
                alt="Imagen de front end logo"
              />
              <div className="card-body text-accent">
                <p className="card-text text-center">{capacity.name}</p>
              </div>
            </div>
          </div>
        );
      })}
    </React.Fragment>
  );
};
