import { Capacity } from '../interfaces/Solution';

export default function useCapabilitiesCard(capatabilities: Capacity[]) {
  let indexArray = 0;
  let cardTotal = 3;
  let countCardAppend = 1;
  const result: Capacity[][] = [];
  const handlerRow = (capacity: Capacity) => {
    if (!result[indexArray]) {
      result[indexArray] = [capacity];
    } else {
      result[indexArray].push(capacity);
    }

    if (countCardAppend === cardTotal) {
      indexArray++;
      countCardAppend = 1;
    } else {
      countCardAppend++;
    }
  };

  capatabilities.forEach(handlerRow);

  return result;

}
