import React from "react";
import { Capacity } from "../../../../../../core/interfaces/Solution";
import { LogoCapabilities } from "../../../../../../core/constants/logo-capabilities";

export const CardArtifactResponsive: React.FC<{
  capabilities: Capacity[];
}> = (props) => {
  return (
    <React.Fragment>
      {props.capabilities.map((capacity, index) => {
        return (
          <div
            key={capacity.name + index}
            className="col-6 d-flex align-items-stretch"
          >
            <div className="card">
              <img
                className="card-header text-center px-0"
                src={LogoCapabilities[capacity.logo]}
                alt="Imagen de front end logo"
              />
              <div className="card-body text-accent px-0">
                <p className="card-text text-center">{capacity.name}</p>
              </div>
            </div>
          </div>
        );
      })}
    </React.Fragment>
  );
};
