import { useState, useEffect } from 'react';
import { SolutionsEntity } from '../../data/repository/solutions-entity';
import SolutionRepository from '../../data/repository/soluctions-repository';

export const useGetSolutions = () => {
  const [state, setState] = useState<SolutionsEntity[]>([]);

  useEffect(() => {
    const solutions = sessionStorage.getItem('solutions');
    if(solutions) {
      setState(JSON.parse(solutions));
      return;
    }

    SolutionRepository.findAll().then(solutions => {
      setState(solutions);
      sessionStorage.setItem('solutions', JSON.stringify(solutions));
    });
  }, []);

  return {
    solutions: state
  };
};
