import React from "react";
import { SolutionsEntity } from "../../../data/repository/solutions-entity";
import { NavBar } from "./components/navbar/navbar.component";
import { SectionArchitecture } from "./components/section/architecture.component";
import { SectionSolutions } from "./components/section/solutions/solutions.component";
import { useGetSolutions } from '../../../core/hooks/useGetSolutions';
import { ConatactUs } from './components/section/contactus.component';
import { SuccessStories } from './components/section/success-stories.component';
import { Footer } from './components/footer/footer.component';

export const IndexView: React.FC<{ solutions: SolutionsEntity[] }> = () => {
  const { solutions } = useGetSolutions();

  return (
    <React.Fragment>
      <NavBar></NavBar>
      <div className="content">
        <SectionArchitecture></SectionArchitecture>
        { solutions.length > 0 && <SectionSolutions solutions={solutions}></SectionSolutions> }
        <SuccessStories></SuccessStories>
        <ConatactUs></ConatactUs>
      </div>
      <Footer></Footer>
    </React.Fragment>
  );
};
