import React, { Fragment, useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { EveButton } from './components/button/button';
import { Information } from './components/information/information';
import { InformationCard } from './components/information-card/information-card';
import { useGetSolutions } from '../../../core/hooks/useGetSolutions';
import { SolutionsEntity } from '../../../data/repository/solutions-entity';
import { Capacity } from '../../../core/interfaces/Solution';

export const DetailSolutionView = () => {
  const history = useHistory();
  const params: any = useParams();
  const { solutions } = useGetSolutions();
  const solutionFiltered = solutions.find(solution => solution.id === params.id);
  const [solution, setSolution] = useState<SolutionsEntity>();
  useEffect(() => {
    setSolution(solutionFiltered);
  }, [solutionFiltered]);

  const goToHome = () => {
    history.push('/');
  };

  return <Fragment>
    <section className="p-md-5 p-4">
      <EveButton handlerClick={goToHome}></EveButton>
    </section>

    {
      solution && solution.detail.capabilities.map((capability: Capacity, index: number) => {
        if (index % 2 === 0) {
          return <section className="p-md-5 p-4">
            <div className="container-lg">
              <div className="row">
                <div className="col-12 col-lg-6 px-lg-5">
                  {
                    solution &&
                    <Information
                      title={solution.name}
                      benefits={solution.detail.benefits}
                      description={solution.detail.description}
                    >
                    </Information>
                  }
                </div>
                <div className="col-12 col-lg-6 text-center px-lg-5">
                  <InformationCard mode="light" name={capability.name} description={capability.description}></InformationCard>
                </div>
              </div>
            </div>
          </section>
        }

        return <section className="p-md-5 p-4 bg-img-dark">
          <div className="container-lg">
            <div className="row">
              <div className="col-12 col-lg-6 text-center px-lg-5 order-2 order-lg-0">
                <InformationCard mode="dark" name={capability.name} description={capability.description}></InformationCard>
              </div>
              <div className="col-12 col-lg-6 text-white  px-lg-5">
                {
                  solution &&
                  <Information
                    title={solution.name}
                    benefits={solution.detail.benefits}
                    description={solution.detail.description}
                  >
                  </Information>
                }
              </div>
            </div>
          </div>
        </section>
      })
    }
  </Fragment>;
};
