import React, { Fragment, FC } from 'react';

export const Information: FC<{ title: string, description: string, benefits: string[] }> =
  ({ title, description, benefits }) => {

    return <Fragment>
      <h2 className="h2">{title}</h2>
      <h6>{description}</h6>
      <h4>Beneficios</h4>
      <ol>
        {
          benefits.map((benefit, index) => <li key={`${benefit}-${index}`}>{benefit}</li>)
        }
      </ol>
    </Fragment>
  };
