import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from 'react-router-dom';
import { IndexView } from './presentation/views/index/index.view';
import { DetailSolutionView } from './presentation/views/detail-solutions/detail-solutions.view';

export const AppRoute = () => {
  return <Router>
    <Switch>
      <Route exact path="/" component={ IndexView }></Route>
      <Route exact path="/detail-solution/:id" component={ DetailSolutionView } ></Route>
      <Redirect to="/"></Redirect>
    </Switch>
  </Router>;
}
