import React from "react";
import { LogoSolutions } from "../../../../../core/constants/logo-solutions";
import { SolutionsEntity } from "../../../../../data/repository/solutions-entity";
import { Link } from "react-router-dom";

export const CardSlideSolutions: React.FC<{ data: SolutionsEntity }> = (
  props
) => {
  return (
    <Link to={`/detail-solution/${props.data.id}`} className="card-dark">
      <img
        className="card-header img-fluid text-center"
        src={LogoSolutions[props.data.logo]}
        alt="Imagen de front end logo"
      />
      <div className="card-body text-white">
        <p className="card-text text-center">{props.data.title}</p>
      </div>
    </Link>
  );
};
