import React from "react";
import SwiperCore, { Navigation, Pagination, A11y } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
import { SolutionsEntity } from "../../../../../data/repository/solutions-entity";
import { CardSlideSolutions } from "./card-slide-solutions.component";

SwiperCore.use([Navigation, Pagination, A11y]);

export const Carrousel: React.FC<{ solutions: SolutionsEntity[] }> = (
  props
) => {
  return (
    <Swiper
      spaceBetween={35}
      width={392}
      slidesPerView={1}
      navigation
      pagination={{ clickable: true }}
    >
      {props.solutions.map((slideContent, index) => {
        return (
          <SwiperSlide
            key={`${slideContent.name}-${index}`}
            virtualIndex={index}
          >
            <CardSlideSolutions data={slideContent}></CardSlideSolutions>
          </SwiperSlide>
        );
      })}
    </Swiper>
  );
};
